"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";
import { ChefHat, Lock } from "lucide-react";
import { createBrowserClient } from "@supabase/ssr";

export default function AdminLogin() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const supabase = createBrowserClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL!,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!,
  );

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    const { error } = await supabase.auth.signInWithPassword({
      email,
      password,
    });

    if (error) {
      alert("Credenciales incorrectas");
      setLoading(false);
    } else {
      router.push("/admin/dashboard");
    }
  };

  return (
    <div className="min-h-screen bg-gray-900 flex items-center justify-center p-4">
      <div className="w-full max-w-sm bg-white rounded-3xl p-8 shadow-2xl">
        <div className="flex justify-center mb-6">
          <div className="bg-black text-white p-4 rounded-2xl shadow-lg">
            <ChefHat size={32} />
          </div>
        </div>

        <h1 className="text-2xl font-black text-center text-gray-900 mb-2">
          D' Irma Admin
        </h1>
        <p className="text-center text-gray-500 mb-8 text-sm">
          Acceso restringido a cocina
        </p>

        <form onSubmit={handleLogin} className="space-y-4">
          <div className="relative">
            <UserIcon />
            <input
              type="email"
              placeholder="admin@dirma.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full pl-10 p-4 bg-gray-50 rounded-xl border-none focus:ring-2 focus:ring-black outline-none font-medium"
            />
          </div>
          <div className="relative">
            <LockIcon />
            <input
              type="password"
              placeholder="••••••••"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full pl-10 p-4 bg-gray-50 rounded-xl border-none focus:ring-2 focus:ring-black outline-none font-medium"
            />
          </div>

          <button
            disabled={loading}
            className="w-full bg-black text-white py-4 rounded-xl font-bold text-lg hover:bg-gray-800 transition-colors disabled:opacity-50"
          >
            {loading ? "Entrando..." : "Entrar a Cocina"}
          </button>
        </form>
      </div>
    </div>
  );
}

// Iconos simples
const UserIcon = () => (
  <div className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400">
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="20"
      height="20"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <path d="M19 21v-2a4 4 0 0 0-4-4H9a4 4 0 0 0-4 4v2" />
      <circle cx="12" cy="7" r="4" />
    </svg>
  </div>
);
const LockIcon = () => (
  <div className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400">
    <Lock size={18} />
  </div>
);
